@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
a h1 {
  font-size: 4rem;
  margin: 2rem auto 2rem auto;
  text-align: center;
  font-family: "Roboto Condensed", sans-serif;
  color: #022d3e;
}
.menu-btn-list{
  padding: .7rem;
}
.menu-button {
  background: #fff;
  border-radius: 5px;
  position: relative;
}
.menu-button a{
  display: flex;
  align-items: center;
  justify-content: center;
}

.menu-button {
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
  flex-grow: 1; 
  background-color:#022D3E;
}

.main-btns{
  width: 100%;
  display:flex;
  align-content: stretch;
  height: 8rem;
}
.group-btn-small{
  display: flex;
  flex-direction: column;
  flex-grow: .6;
}
.square-btn-lg{
  margin-right:.7rem;
}
.square-btn-small:first-child{
  margin-bottom:.5rem ;
}
.full-width-btn{
  margin-top:1.5rem;
 
}
.full-width-btn a{
  padding-top: 1.2rem;
  padding-bottom: 1.2rem;
}
.btn-text {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.btn-text {
  padding: 22px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

i.fas.fa-boxes {
  font-size: 2.5rem;
  padding:1rem;
}
@media only screen and (min-width: 600px) {
 
}

.category-name {
  display: flex;
  align-items: center;
  background: #022d3e;
  color: white;
  padding: 10px;
  margin-bottom: 1rem;
  border-radius: 5px;
}
.category-name a {
  display: flex;
  justify-content: center;
}

.category-name a * {
  margin: 0 1rem;
}
.random-list {
  display: flex;
  width: 90%;
  flex-direction: column;
  color:black;
  background: white;
  margin: 0 auto 2rem auto;
}
.random-list button {
  width: 100%;
  font-size: 1.3rem;
}
.list-btn-group div:nth-child(1) {
  margin-right:0.5rem;
}
.list-btn-group div {
  flex-grow: 1;
  padding:10px;    
}
.listed-item {
  border-bottom: 2px solid #022D3E;
  display:flex;
}

.listed-item:hover {
  border: 2px solid #004e98;
  color:#004e98;
  font-size: 1.1rem;
}
.listed-item:active {
  border: 2px solid #022D3E;
  color:white;
  background: #022D3E;
  font-size: 1.2rem;
}

.listed-item a {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.list{
  margin-top:.5rem;
}


.item-form{
  color: #022D3E;
  font-size:1.2rem;
  display: flex;
  flex-direction: column;
  z-index: -1;
}

.item-form form label span{
  display: inline-block;
  text-align: right;

}
.item-form form label input{
  border:none;
  border-bottom: 1px solid #022D3E;
}
.item-form form label input:focus{
  outline: none;
  border:none;
  border-bottom: 1px solid #03FE89;
}
.item-form form label{
  display: flex;
}
.item-form form label span{
  display: inline-block;
  text-align: right;
  margin-right:10px;
  width: 40%;
}
@media only screen and (min-width: 600px) {
  .form-btn-group{
    width: 50%;
    margin: 0 auto;
  }
}


.item-container {
  display: flex;
  flex-direction:column;
  padding: .3rem;
  
  color: black;
}
.item-container p{
  font-size:1.3rem;
}
.item-amount {
  display: flex;
  flex-direction:column;
  height: 40vh;
  text-align: center;
  margin-bottom: 3rem;
}

.item-amount p:nth-child(1) {
  font-size: 9rem;
  padding-bottom: 0;
}
.item-amount p span{
  font-size:1.5rem;
}
.item-amount p:nth-child(2) {
  padding: 1.5rem;
  padding-top: 0;
  font-size: 2rem;
  font-weight: bold;
}

.item-btn-group {
  display: flex;
  justify-content: space-around;
  margin-top: 2rem;
}

.item-btn {
  display:flex;
  flex-grow: 1;
  margin:.2rem;
  padding: .5rem;
  background: #022D3E;
  color: white;
  border-radius: 5%;
  height: 2rem;
  justify-content: center;
  align-items: center;

}
.item-btn a{display:flex;
justify-content: center;
align-items: center;
}
.quick-modify {
  display: flex;
  height: 2rem;
  margin-bottom: 1rem;
  align-items: center;
}

.quick-modify input {
  width: 50%;
  height:inherit;
}


.quick-modify div{
  width: 25%;
  margin-left: .5rem;
}

i.fas.fa-plus-circle {
  font-size: 2.5rem;
  color: #022D3E;
}
i.fas.fa-minus-circle {
  font-size: 2.5rem;
  color: red;
}
input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
