/* batch */ 
.batch-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #022d3e;
}
.batch-container caption {
  font-size: 1.5rem;
  font-weight: bold;
}
table {
  display: ;
  border-collapse: collapse;
  
}

td,
th {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
}
tr {
  color: black;
}

tr:nth-child(even) {
  background-color: #dddddd;
}


.confirm {
  background: #03fe89;
}
.batch-items{
  display: flex;
  width: 90%;
  flex-direction: column;
}

.batch-form-container{
  color:#022D3E;
}
.batch-form-container form{
  padding: 1.5rem;
  display: flex;
  flex-direction: column;
}
.batch-items-form {
  display: flex;
  border:2px solid #022D3E;
  padding: .5rem;
  margin-bottom: 1rem;
}
.batch-item{
  flex: 2 2 80%;
}
.batch-item {
  display: flex;
  flex-direction: column;
}
.del-item{
  display:flex;
  flex: 1 1 20%;
  align-items: center;
  justify-content: center;
}
.del-item:hover{
  font-weight: bolder;
}
.del-item:active{
  font-weight: bolder;
  color: red;
}
.batch-item{
  flex: 1 1 80%;
}
.batch-form form label{
  display: block;
}
.batch-form form label span{
  display: inline-block;
  text-align: left;
  margin-right:10px;
  width: 40%;
}
.add-item-batch{
  border:1px solid #022D3E;
  background: white;
  color: #022D3E;
  width: 40%;
  padding: .5rem;
}
.add-item-batch:hover{
  font-weight: bold;
}
.add-item-batch:active{
  border:2px solid #022D3E;
  background: #022D3E;
  color: white;
}
@media only screen and (min-width: 600px) {
  .batch-form form label span{
    display: inline-block;
    text-align: right;
    margin-right:10px;
    width: 50%;
  }
  .batch-item{
    flex: 2 2 80%;
  }
}

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    display:flex;
    background: white;
    font-family: 'Roboto', sans-serif;
    color:white;
  
  }
p{
  margin:0;
}
a {
  text-decoration: none;
  color: inherit;
  width: 100%;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
div#root {
  width: 100%;
  padding: .5rem;

}
.form-btn-group{
  margin-top:3rem;
}
.form-btn,.form-btn a {
  display:flex;
  padding: .5rem;
  background: #022D3E;
  color: white;
  border-radius: 5%;
  height: 2rem;
  justify-content: center;
  align-items: center;
}
.form-btn.reply {
  margin-bottom: 1rem;
  margin-top:.5rem;
}

.delete {
  background: red;
}

.btn-text span {
  font-size: smaller;
}

.main-btns {
  height: 9rem;
}
.list-btn-group {
  display: flex;
  width: 100%;
}
.search {
  z-index: 1;
  align-self: center;
  margin-bottom: 1.5rem;
  border: 1px solid #022d3e;
  width: 60%;
  height: 2rem;
  border-radius: 5%;
  padding: 5px;
}
.search:focus-within {
  border: 2px solid #03fe89;
}
.search input {
  outline: none;
  border: none;
}
.search input[type="text"] {
  text-align: center;
  color: #022d3e;
}
.search-results {
  margin-top: 1rem;
  background: white;
  max-height: 50vh;
  overflow-y: scroll;
}
.search-results a:hover {
  border-bottom: 2px solid #022d3e;
}
.search label {
  display: flex;
  align-items: center;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  height: 100%;
}
.search label input {
  display: flex;
  height: inherit;
  overflow: overlay;
  margin-left: 1rem;
}

.search label span {
  margin-left: 1rem;
}
.search-results a h4 {
  margin: 0;
  padding: 1rem;
  border-bottom: 1px solid #022d3e;
}
.search-results a h4:hover {
  border-bottom: 2px solid #03fe89;
}
.form-btn.delete {
  margin-top: 0.5rem;
  background: red;
}
.button{
  cursor: pointer;
}
.button:active {
  background: #004e98;
}
.button:hover {
  background: #004e98;
}

.listed-item a {
  display: flex;
  width: 100%;
}

.listed-item a h4 {
  margin: 0;
  flex: 1 1;
  text-align: center;
}

.listed-item a span {
  flex: 0.2 1;
  align-self: flex-end;
}

i.fas.fa-caret-right {
  font-size: 3rem;
}

.listed-item {
  border: 2px solid #022d3e;
  border-radius: 5%;
  margin: inherit;
  color: #022d3e;
  font-size: large;
} 
@media only screen and (min-width: 600px) {
  div#root {
    width: 55%;
    margin: 0 auto;
  }
  .item-form form{
    margin: 0 auto;
    width: 55%;
  } 
}
